:root {
  --font-family-main:
          optimistic display,
          -apple-system,
          ui-sans-serif,
          system-ui,
          blinkmacsystemfont,
          segoe ui,
          roboto,
          helvetica neue,
          arial,
          noto sans,
          sans-serif,
          apple color emoji,
          segoe ui emoji,
          segoe ui symbol,
          noto color emoji;
  --font-size-m: 16px;
  --font-line-m: 24px;
  --font-m: var(--font-size-m) / var(--font-line-m) var(--font-family-main);
  --font-size-l: 24px;
  --font-line-l: 32px;
  --font-l: var(--font-size-l) / var(--font-line-l) var(--font-family-main);

  // Размеры
  --navbar-height: 50px;
  --sidebar-width: 20rem;
  --sidebar-width-collapsed: 5rem;

  // z-indexes
  --modal-z-index: 10;

  // colors
  --red-light: #f00;
  --red-dark: #b50404;
  --primary-button-color: #6c6cd7;
  --primary-color: black;
  --primary-bg-color: #fff;

}
